<template>
    <div class="block-header-container"
         :class="device_platform">
        <div class="content-container">
            <div class="logo-name-container">
                <div class="logo"
                     @click="getCall({type: 'sale', form:'header'})"></div>
                <div class="offices-name-container"
                     @click="getCall({type: 'sale', form:'header'})">
                    <div class="office-brand">
                        {{CONSTANTS.brand}}
                    </div>
                    <div class="office-name">
                        официальный дилер
                    </div>
                </div>
            </div>

            <div class="address-worktime-container">
                <div class="office-address-container" v-for="office in offices">
                    <div class="office-phone" v-for="office in offices">
                        <a :href="'tel:' + office.phone_raw" class="phone" id="phone">
                            {{office.phone}}
                        </a>
                    </div>
                    <div class="office-address" @click="scrollTo('contacts')">
                        {{office.address}}
                    </div>
                    <div class="office-worktime" v-html="office.worktime"></div>
                </div>
            </div>

            <div class="orange-btn" @click="getCall({type: 'sale', form:'header', text: 'Получить выгодные условия'})">
                Обратный звонок
                <div class="btn-arrow"></div>
            </div>
            <div class="logo-dealer"
                 @click="getCall({type: 'sale', form:'header'})"></div>
        </div>
    </div>
</template>

<script>
    import Mixin from '../common/mixin';

    export default {
        name: 'block-header',
        components: {},
        directives: {},
        mixins: [Mixin],
        data() {
            let offices = this.CONSTANTS.offices;
            return {
                offices: offices
            }
        },
        methods: {
            scrollTo(where) {
                let newhash = '#' + where;
                history.replaceState(null, null, newhash);
                this.$emit('scrollTo', where);
            },
            getCall(data) {
                this.$emit('getCall', data);
            }
        }
    }
</script>

