<template>
    <div class="block-main-container"
         :class="[device_platform]">
        <div class="top-cover"></div>
        <div class="bottom-cover" v-if="device_platform !== 'mobile'"></div>
        <div class="arrow arrow-left" v-if="device_platform !== 'mobile' && array_of_slides.length > 1"></div>
        <div class="arrow arrow-right" v-if="device_platform !== 'mobile'  && array_of_slides.length > 1"></div>
        <div class="swiper-container-main">
            <div class="swiper-wrapper">
                <div class="swiper-slide"
                     v-for="i in array_of_slides"
                     :key="i">
                    <div class="content-container" v-if="i === 1" :class="'_' + i">
                        <div class="cars-img" :class="'_' + i"></div>
                        <div class="text-container">
                            <div class="header">
                                Спецпредложения на LADA
                            </div>
                            <div class="central-container">
                                <callback-input :data="{type: 'sale', form: 'main'}"
                                                :horizontal="device_platform === 'desktop'"
                                                class="main-input"
                                                v-on="$listeners">
                                    Получить предложение
                                </callback-input>
                                <div class="advantage">
                                    <div>Выгода</div>
                                    <div class="big-numba-container">до <span class="big-numba">220 000</span> ₽</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <block-clock class="block-clock" v-if="device_platform !== 'mobile'"></block-clock>
    </div>
</template>


<script>
    import Mixin from '../common/mixin';
    import finance from '../common/finance';
    import CallbackInput from "./callback-form/callback-input";
    import BlockClock from './block-clock';
    import Swiper from 'swiper';
    import $ from 'jquery';
    import Inputmask from 'inputmask';

    export default {
        name: "block-main",
        components: {BlockClock, CallbackInput},
        mixins: [Mixin, finance],
        data() {
            return {
                swiper: null,
                array_of_slides: [1]
            }
        },
        mounted() {
            this.initSwiper();
            window.addEventListener('click', event => {
                if (!event.target || !event.target.id) {
                    return;
                }
                if (event.target.id === 'btn-common-1') {
                    this.getCall({type: 'sale', form: 'main'})
                }
            });

        },
        watch: {
            device_platform() {
                if (this.swiper) {
                    this.swiper.destroy();
                    this.swiper = null;
                    this.$nextTick(this.initSwiper);
                }
            }
        },
        methods: {
            getAgreement() {
                this.$emit('getAgreement');
            },
            initSwiper() {
                if (this.swiper) return;
                this.swiper = new Swiper('.swiper-container-main', {
                    loop: this.array_of_slides.length > 1,
                    simulateTouch: false,
                    autoplay: this.array_of_slides.length > 1 ? {
                        delay: 5000,
                    } : false,
                    navigation: {
                        nextEl: '.arrow-right',
                        prevEl: '.arrow-left',
                    },
                });

                if (this.swiper.loopedSlides) {
                    //повесить маску на input в дубликате
                    let dublicate = $('.swiper-slide-duplicate'),
                        dublicate_inputs = dublicate.find('input[type=tel]');

                    dublicate_inputs.each((index) => {
                        let el = dublicate_inputs.get(index),
                            elem = $(el);

                        Inputmask({mask: '(+7|8) (999) 999-9999'}).mask(el);
                        elem.on('focus', () => {
                            if (!el.value.length) {
                                setTimeout(() => {
                                    el.setSelectionRange(4,4);
                                }, 0);
                            }
                        }).on('blur', () => {
                            if (el.value.length < 5) {
                                el.value = '';
                            }
                        });
                    });

                    //перевесить megatimer на баннер, если он есть. В нижнем коде учитывается, что номер первого слайда - 1,
                    //а блок с мегатаймером имеет id timer_block

                    // setTimeout(() => {
                    //     let dublicate = $('.swiper-slide-duplicate'),
                    //         dublicate_content = dublicate.find('.content-container._1'),
                    //         dublicate_bclock = dublicate_content.find('.block-clock-container').find('.bclock-clock'),
                    //         clock = $('#timer_block');
                    //
                    //     dublicate_bclock.empty();
                    //     dublicate_bclock.get(0).appendChild(clock.clone().get(0));
                    // }, 500);
                }
            },
        }
    }
</script>


