<template>
    <div class="main-container" :class="[device_platform]">
        <div class="block-clock-container width-container" >
            <div class="bclock-header">
                Окончание акции через:
            </div>
            <div class="bclock-clock">
                <div v-for="stat in date_parts" class="bclock-clock-stat">
                    <div class="bclock-clock-stat__value-container">
                        <div class="bclock-clock-stat__value no-select">
                            {{timeElement(stat)[0]}}
                        </div>
                        <div class="bclock-clock-stat__value no-select">
                            {{timeElement(stat)[1]}}
                        </div>
                        <div v-if="!(device_platform === 'mobile' && stat.name.includes('час'))"
                             class="separator"> : </div>
                    </div>

                    <div class="bclock-clock-stat__name no-select">
                        {{stat.name}}
                    </div>
                </div>

            </div>
        </div>
<!--        <div class="instruction">-->

<!--            <p>Звоните <span><a :href="'tel:' + CONSTANTS.phone_raw" class="phone">{{CONSTANTS.phone}}</a></span></p>-->
<!--        </div>-->
<!--        <div class="blue-btn"-->
<!--             @click="getCall({type: 'sale', form:'clock'})">-->
<!--            Получить предложение-->

<!--        </div>-->
    </div>
</template>

<script>
    import Mixin from '../common/mixin';
    import CF from '../common/common-functions';

    export default {
        name: 'block-clock',
        components: {},
        directives: {},
        mixins: [Mixin],
        props: [''],
        data() {
            return {
                date_parts: [
                    {
                        name: 'Дней',
                        units: 'days',
                        value: 0,
                        period: 0,
                    },
                    {
                        name: 'Часов',
                        units: 'hours',
                        value: 0,
                        period: 24,
                    },
                    {
                        name: 'Минут',
                        units: 'minutes',
                        value: 0,
                        period: 60,
                    },
                    {
                        name: 'Секунд',
                        units: 'seconds',
                        value: 0,
                        period: 60,
                    }
                ]
            }
        },
        created() {
            let small_part_multiplier = 1,
                time_left = (this.CONSTANTS.end_date.getTime() - Date.now()) / 1000;
            for (let i = this.date_parts.length - 1; i >= 0; --i) {
                let part = this.date_parts[i];
                part.value = Math.floor((time_left / small_part_multiplier) % (part.period || 10000));
                part.name = this.getName(part);
                small_part_multiplier = small_part_multiplier * part.period;
            }
            setInterval(this.clockTick, 1000);
        },
        computed: {
        },
        methods: {
            clockTick() {
                for (let i = this.date_parts.length - 1; i >= 0; --i) {
                    if (this.date_parts[i].value > 0) {
                        --this.date_parts[i].value;
                        this.date_parts[i].name = this.getName(this.date_parts[i]);
                        return;
                    }
                    this.date_parts[i].value = this.date_parts[i].period - 1;
                    this.date_parts[i].name = this.getName(this.date_parts[i]);
                }
            },
            getName(date_part) {
                return CF.getNumericalName(date_part.value, date_part.units);
            },
            timeElement(stat) {
                let time = stat.value.toString();
                if (time.length < 2) {
                    time = '0' + time;
                }
                return time
            }
        }
    }
</script>

