var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"agreement-text"},[_c('div',{staticClass:"agreement-header"},[_vm._v("\n                Согласие на обработку запроса\n            ")]),_c('div',{staticClass:"agreement-part"},[_c('b',[_vm._v("Конфиденциальность и условия использования.")]),_c('br'),_c('br'),_vm._v("\n                Приветствуем Вас на сайте callkeeper.ru. Мы рады проявленному Вами интересу к нашей\n                компании и оказываемым нами услугам. ООО «КоллКипер» со всей серьезностью\n                относится к соблюдению права каждого посетителя на конфиденциальность в отношении\n                персональных данных и принимает физические, административные и технические меры\n                по защите любой информации, предоставляемой в режиме «онлайн». В большинстве\n                случаев, при посещении сайта callkeeper.ru, пользователи не сообщают и не раскрывают\n                никакой информации, относящейся к персональным данным. Однако, при использовании\n                специальных возможностей сайта, требуется предоставление такой информации, причем в\n                исключительно необходимом для этого объеме. Обработка персональных данных\n                посетителей производится в соответствии с требованиями Федерального закона №152-ФЗ\n                «О персональных данных» от 27 июля 2006 года. ООО «КоллКипер» является\n                зарегистрированным оператором персональных данных № 77-16-004732 в реестре\n                Роскомнадзора. Данные, которые мы собираем о пользователях в России, безопасно\n                хранятся на серверах, расположенных в Российской Федерации.\n                "),_c('br'),_c('br'),_vm._v("\n                Просим Вас ознакомиться с содержанием настоящего соглашения о конфиденциальности\n                и защите персональных данных (далее — «Соглашение»). Если во время посещения сайта\n                Вы принимаете решение зарегистрироваться или предоставить Ваши персональные\n                данные, Вы тем самым даете свое безоговорочное согласие на использование указанной\n                информации ООО «КоллКипер» в соответствии с условиями настоящего Соглашения. В\n                случае несогласия с этими условиями, Вам следует воздержаться от просмотра нашего\n                веб-сайта и использования сервиса CallKeeper.\n                "),_c('br'),_c('br'),_vm._v("\n                Если Вы являетесь посетителем стороннего веб-сайта, на котором используется сервис\n                CallKeeper, то любые предоставляемые вами данные на данном ресурсе собираются\n                в соответствии с политикой о персональных данных данного веб-сайта (компании).\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Состав персональных данных.")]),_c('br'),_c('br'),_vm._v("\n                Персональные данные это любая информация, относящаяся к прямо или косвенно\n                определенному, или определяемому физическому лицу. К такой информации, в частности,\n                можно отнести: ФИО, год, месяц, дата и место рождения, адрес, сведения о семейном,\n                социальном, имущественном положении, сведения об образовании, профессии, доходах,\n                а также другую информацию.\n                "),_c('br'),_c('br'),_vm._v("\n                Все обрабатываемые нами персональные данные являются конфиденциальной, строго\n                охраняемой информацией в соответствии с применимым законодательством.\n                Обработка персональных данных это любое действие (операция) или совокупность\n                действий (операций) с персональными данными, совершаемых с использованием средств\n                автоматизации или без использования таких средств. К таким действиям (операциям)\n                можно отнести: сбор, получение, запись, систематизацию, накопление, хранение,\n                уточнение (обновление, изменение), извлечение, использование, передачу\n                (распространение, предоставление, доступ), обезличивание, блокирование, удаление,\n                уничтожение персональных данных.\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Сбор и обработка персональных данных.")]),_c('br'),_c('br'),_vm._v("\n                ООО «КоллКипер» осуществляет сбор только той персональной информации, которую\n                пользователь предоставляет добровольно. Персональные данные, собираемые нами могут\n                включать, но не ограничиваться следующими данными: Ваше имя, адрес электронной\n                почты, номер телефона.\n                "),_c('br'),_c('br'),_vm._v("\n                Любая предоставленная Вами при использовании настоящего сайта информация,\n                относящаяся к персональным данным, будет использована ООО «КоллКипер» только для\n                целей, указанных ниже. Предоставляя информацию при использовании настоящего сайта,\n                а также посредством любых иных обращений в ООО «КоллКипер», Вы выражаете свое\n                согласие с тем, что соответствующая информация может хранится и использоваться ООО\n                «КоллКипер» в настоящее время и в дальнейшем в целях:\n                "),_c('br'),_c('br'),_vm._v("\n                - предоставления доступа к Сервису CallKeeper;\n                "),_c('br'),_vm._v("\n                - предоставления Вам информации о продуктах, услугах и мероприятиях, которые могут\n                представлять для Вас интерес;\n                "),_c('br'),_vm._v("\n                - проведения маркетинговых исследований для улучшения качества продукции и услуг\n                ООО «КоллКипер»;\n                "),_c('br'),_vm._v("\n                - выполнение ООО «КоллКипер» своих обязательств, в рамках оказываемых услуг, на\n                основании заключенных договоров (выполнение договорных обязательств);\n                "),_c('br'),_vm._v("\n                - ответов на Ваши запросы;\n                "),_c('br'),_vm._v("\n                - технического администрирования страниц сайта;\n                "),_c('br'),_vm._v("\n                - управления клиентской базой;\n                "),_c('br'),_vm._v("\n                - проведения анкетных опросов по продукции и маркетинга;\n                "),_c('br'),_vm._v("\n                - а также в целях предоставления субъекту персональных данных информации\n                коммерческого и информационного характера через различные каналы связи, в том числе\n                по почте, смс, электронной почте, телефону, если субъект персональных данных изъявит\n                желание на получение подобной информации соответствующими средствами связи.\n                "),_c('br'),_c('br'),_vm._v("\n                Сбору подлежат такие данные как фамилия, имя, почтовый адрес, адрес электронной\n                почты, номер телефона, а также некоторая вторичная информация, например, файлы\n                cookies, данные о подключении к сети и системная информация.\n                "),_c('br'),_c('br'),_vm._v("\n                Кроме того, мы можем использовать сторонние сервисы (такие как Google Analytics,\n                Яндекс.Метрика). Эти сторонние сервисы имеют свои собственные политики\n                конфиденциальности, описывающие как они используют предоставленную информацию.\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Контроль и разрешение использования персональных данных.")]),_c('br'),_c('br'),_vm._v("\n                ООО «КоллКипер» принимает все необходимые меры для защиты любых персональных\n                данных, предоставляемых компании. Доступ к персональным данным имеют только\n                уполномоченные сотрудники, уполномоченные сотрудники сторонних компаний\n                (деловых партнеров), подписавшие соглашение о конфиденциальности и защите\n                персональных данных. Все сотрудники ООО «КоллКипер», имеющие доступ к\n                персональным данным, должны придерживаться политики по обеспечению\n                конфиденциальности и защиты персональных данных. В целях обеспечения\n                конфиденциальности информации и защиты персональных данных ООО «КоллКипер»\n                принимает все меры, необходимые для предотвращения несанкционированного доступа.\n                Кроме того, персональные данные могут передаваться государственным органам в тех\n                случаях, если ООО «КоллКипер» вынуждено сделать это в соответствии с требованиями\n                применимого законодательства, либо если, по мнению ООО «КоллКипер», такое действие\n                необходимо для соблюдения законности, выполнения законно обоснованных требований,\n                либо в целях защиты прав ООО «КоллКипер», его заказчиков и потребителей. Все\n                контрагенты ООО «КоллКипер», которым могут быть переданы Ваши персональные\n                данные действуют в соответствии с договором, налагающим на них соответствующие\n                правовые обязательства. Такие контрагенты обеспечивают высокий стандарт в сфере\n                защиты персональных данных, и гарантируют принятие всех технических мер,\n                необходимых для безопасного хранения персональных данных на протяжении всего\n                предусмотренного периода. В соответствии с Федеральным законом РФ № 152-ФЗ «О\n                персональных данных» хранение персональных данных в ООО «КоллКипер»\n                осуществляется в форме, позволяющей определить субъекта персональных данных, не\n                дольше, чем этого требуют цели их обработки, и они подлежат уничтожению по\n                достижении целей обработки или в случае утраты необходимости в их достижении.\n                Пользователь может потребовать удалить, исправить или проверить персональные данные\n                путем отправки электронного сообщения или письма по адресу, i@callkeeper.ru, либо\n                позвонить по телефону. Контактная информация: ООО «КоллКипер» 140003, Московская\n                обл, Люберецкий р-н, г Люберцы, ул 3-е Почтовое отделение, д 98, офис 117 Тел.: +7\n                495 989-84-74.\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Отказ предоставления")]),_c('br'),_c('br'),_vm._v("\n                Мы предоставляем Вам возможность отказаться от предоставления нам Ваших\n                персональных данных, когда мы запрашиваем их.\n                "),_c('br'),_vm._v("\n                Если Вы решите отказаться от предоставления персональных данных, мы не сможем\n                предоставлять Вам доступ к Сервису CallKeeper.\n                "),_c('br'),_vm._v("\n                Отказ от предоставления Ваших персональных данных Вы можете отправить по адресу\n                i@callkeeper.ru\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Удаление")]),_c('br'),_c('br'),_vm._v("\n                Вы можете запросить удаление Ваших персональных данных из Сервиса CallKeeper.\n                По такому запросу мы удалим все собранные о Вас персональные данные и не сможем\n                более предоставлять доступ к Сервису CallKeeper.\n                "),_c('br'),_c('br'),_vm._v("\n                Для удаления персональных данных о Вас, обратитесь по адресу i@callkeeper.ru\n                "),_c('br'),_c('br'),_vm._v("\n                Мы можем отказаться обрабатывать запросы, которые необоснованно повторяются или\n                систематичны, требуют непропорциональных технических усилий (например, запросов,\n                касающихся данных, хранящейся на резервных носителях), ставящих под угрозу\n                конфиденциальность других пользователей. В любом случае, когда мы удаляем или\n                изменяем данные, мы выполняем эту услугу бесплатно, за исключением случаев, когда\n                это потребует непропорциональных усилий.\n                "),_c('br'),_c('br'),_vm._v("\n                Обратите внимание, что данные, которые не относятся к персональным могут\n                сохраняться, даже если вы прекратите использование Сервиса CallKeeper или запросите\n                удаление персональных данных.\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Согласие")]),_c('br'),_c('br'),_vm._v("\n                Вы можете отказаться от получения информационных и рекламных материалов\n                от CallKeeper. Для этого воспользуйтесь соответствующей настройкой «отписаться»\n                в рассылаемых материалах, или напишите свой запрос по адресу i@callkeeper.ru\n                "),_c('br'),_c('br'),_c('b',[_vm._v("Файлы cookies")]),_c('br'),_c('br'),_vm._v("\n                Файлы cookie представляют собой небольшие текстовые файлы, которые сохраняются в\n                браузере Вашего компьютера или мобильного телефона после посещения веб-сайтов.\n                Файлы cookie широко применяются для поддержания работы веб-сайтов или для\n                повышения качества пользовательского опыта, а также для предоставления определенной\n                информации владельцам веб-сайта. Файлы cookie, которые используем мы, могут\n                включать в себя информацию о Ваших предпочтениях в Интернете, чтобы мы могли\n                сделать наш веб-сайт максимально интересным для Вас.\n                "),_c('br'),_c('br'),_vm._v("\n                Персональные данные сохраняются в cookies только в том случае, если Вы даете на это\n                разрешение, например, чтобы облегчить защищенный доступ в Интернет и не вводить\n                повторно ID пользователя и пароль. Просмотры нашего сайта возможны и без cookies. В\n                большинстве браузеров прием cookies осуществляется автоматически.\n                "),_c('br'),_c('br'),_vm._v("\n                Как можно разрешить или запретить использование файлов cookie?\n                "),_c('br'),_c('br'),_vm._v("\n                В настройках своего устройства пользователь может разрешить использование всех\n                файлов cookie, у